.container {
    width: 100%;
    max-width: 800px;
    margin: 32px auto;
    border-radius: 4px;
    box-shadow: rgba(0, 0, 0, 0.20%) 0px 2px 1px -1px, 
                rgba(0, 0, 0, 0.14%) 0px 1px 1px 0px, 
                rgba(0, 0, 0, 0.12%) 0px 1px 3px 0px;
    padding: 16px;

    ul {
        padding: inherit;
        margin: inherit;
    }

    a {
        color: blue;
        text-decoration: underline;
    }
}